
.displayPickerWrapper {
    text-align: right;
    margin: 10px 10%;
    width: 80%;
    position: absolute;
    bottom: 15px;
}

.displayPicker {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.text {
    composes: instructions from "Sidebar.module.css";
}

.displayOption {
    flex-grow: 1;
    min-width: 40px;
    min-height: 80px;
    max-width: 43%;
    border: solid;
    display: flex;
    margin: 0 3px;
    color: #FF00DA;
    flex-direction: column;
    justify-content: center;
}

.displayOption > span {
    width: 65%;
    height: 10px;
    margin: auto;
}

