@font-face {
    font-family: Lech;
    src: url("../aaLech.otf");
}

@font-face {
    font-family: Semek;
    src: url("../aasemek-Regular.otf");
}

.Lech {
    font-family: Lech, sans-serif;
}

.Semek {
    font-family: Semek, sans-serif;
}


.sideBar {
    grid-row: 2;
    grid-column: 1;
    color: #FF00DA;
    position: relative;
}

.shoutMenuWrapper {
    margin: 10%;
    font-size: .9em;
    text-align: right;
}

.shoutMenu {
    border: 10px solid #FF00DA;
    margin-bottom: 5px;
    padding-right: 2.5ch;
    padding-bottom: 1.5ch;
}

.shoutMenu h1 {
    margin: 10px 0;
    text-align: right;
    direction: rtl;
    letter-spacing: .06ch;
    font-size: 2.5em;
}

.instructions {
    line-height: 2.2ex;
    padding-right: .3ch;
}

.line {
    width: 80%;
    margin-right: 0;
    display: block;
    background: #FF00DA;
    height: 5px;
}

.shoutMenu ul {
    list-style: none;
    padding-right: 0;
    font-size: 2.5em;
    margin: 1ex 0;
}

.shoutMenu li:first-child {
    margin-right: .5ch;
}

.shoutMenu li {
    margin-top: .5ex;
}

