

.title {
    font-family: Haim, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    color: white;
    font-size: 60px;
    margin: 50px;
    letter-spacing: .06ch;
}

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;


    grid-row: 1;
    grid-column: 1 / 3;

    background: #FF00DA;
    width: 100%;
    padding: 5px 4px;
}

.pinkFront {
    height: 60%;
    width: auto;
    margin-left: 50px;
}