
@font-face {
  font-family: Haim;
  src: url("Haim-Medium.otf");
  unicode-range: U+05BE-05F4;
}

@font-face {
  font-family: Secular;
  src: url("SecularOne-Regular.ttf");

}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: Haim, Secular, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  color: white;
  text-align: right;
  overflow-y: hidden;
  overflow-x: hidden;
  height: 100vh;
  direction: rtl;
}

#root {
  height: 100%;
}

.unresponsive-text {
  cursor: default;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}