
.singleDisplayWrapper {
    border: solid;
    width: 100%;
    height: 100%;
    min-height: 150px;
    min-width: 200px;
}

.defaultDisplayWrapper {
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    border-width: 20px !important;
}
.defaultDisplayWrapper div {
    width: 100%;
    height: 100%;
    min-height: 150px;
    min-width: 200px;
}

.defaultDisplayWrapper canvas {
    width: 100% !important;
    height: 100% !important;
}

.namedShoutObject {
    color: black;
    text-align: right;
    font-size: 1.5em;
}

.multipleDisplay {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 50px;
    row-gap: 50px;
    margin: 20px;
}

.shoutDisplay {
    grid-column: 2;
    grid-row: 2;
    margin: 50px;
}