
.black {
    border: 10px solid black;
    color: black;
    background: white;
}

.black > span {
    background: black;
    display: block;
}

.pink {
    border: 10px solid #FF00DA;
    color: #FF00DA;
    background-size: cover;
}

.pink > span {
    display: block;
    background: #FF00DA;
}